export async  function  getmynftList(library, account, chainID){
    const MyNftcontract = getMyNftcontract(library, account, chainID);
    console.log(MyNftcontract);
    let balance  = await MyNftcontract.balanceOf(account);
    // balance=Web3.utils.hexToNumber(balance);
    balance=balance.toNumber();
    let index = 0;

    const list =[];
    for (index; index < balance; index++) {
        console.log('---');
        const token   = await MyNftcontract.tokenOfOwnerByIndex(account,index);

        list.push({
          tokenID:token.toNumber(),
          tokenAddress:MyNftcontract.address
        });
    }

    return list;
    

}

