getsellToken.prototype.xxx="sssss";
getsellToken.prototype.addnft= function(Nftcontractaddress,tokenId,Price){
      // NFT资产
      const tokenInfo = {
        address: Nftcontractaddress,
        tokenId: tokenId.toString()
    };

    // 编码tokenInfo
    const data = ethabi.encodeParameter(
        'tuple(address, uint256)[]',
      [[tokenInfo.address, tokenInfo.tokenId]]
    );

    // 设置tokenId为1的NFT, 价格为1ETH
    const sellToken = {
        price: toWei(Price),
        data: data
      };
      this.list.push(sellToken);
      this.originallist.push(tokenInfo);
};

getsellToken.prototype.addnftcollection=function(Nftcontractaddress,Price){
      // 对整个collection出offer, tokenId写为0
      const collectionTokenInfo = {
        address: Nftcontractaddress,
        tokenId: '0'
      };
  
      
      const data = ethabi.encodeParameter(
          'tuple(address, uint256)[]',
        [[collectionTokenInfo.address, collectionTokenInfo.tokenId]]
      );
  
      const buyToken = {
        price: toWei(Price),
        data: data
      };
      this.list=[];
      this.originallist=[];
      this.list.push(buyToken);
      this.originallist.push(collectionTokenInfo);

};

