getsellToken.prototype.buildOrder = async function(library, account, chainID,expireTime,unit,orderType,dataMask){
  this.orderForServer={};
  const sellTokens = this.getList();
   if(!(sellTokens instanceof Array)){
     console.log("need sellTokens");
   }

   const fees=[];

   sellTokens.forEach((item)=>{
    fees.push([
      {
        percentage: 2500,
        to: swapfeeaddress
      },
      {
        percentage: 5000,
        to: royaltiesaddress
      }
    ]);

   });


   // 订单详情
   const orderInfo = {
       salt: generatePseudoRandomSalt().toString(),  // 随机种子
       user: account, // 卖家地址
       network: chainID, // 当前网络的chainId
       intent: orderType, // 卖单
       delegateType: 1, // 写死为1
       deadline: expireTime, // 过期时间
       currency: this.getunitaddress(chainID,unit), // 出售ERC20的地址, 如果为ETH原生资产, 参数为0x000...0
       dataMask: dataMask, // 卖单写死为'0x'
       items: sellTokens, // 本次卖单的token, 可以是多个 sellToken1 sellToken2
       fees: fees, //有多个sellTokens 就有多个fees
           
        // fees为订单收取的手续费用, 如果有多个卖单, 需要为每一个卖单设置手续费
         // 这里fee是一个数组, 数组第一项表示交易所收取的费用, 2500表示0.25%, 数组第二项表示NFT的版税
         // 注意: 每一个fee必须有两个item, 一个交易所地址和版税收取地址, percentage可为0, to可为ZERO_ADDRESS
     };

     const orderItemPattern = orderInfo.items.map(item => [item.price, item.data]);
   const feeItemPattern = orderInfo.fees.map(item => item.map(e => [e.percentage, e.to]));

   const orderHash = keccak256(ethabi.encodeParameters(
       ['uint256', 'address', 'uint256', 'uint256', 'uint256', 'uint256',
         'address', 'bytes', 'uint256', 'tuple(uint256, bytes)[]', 'uint256', 'tuple(uint256, address)[][]'],
       [
         orderInfo.salt, orderInfo.user, orderInfo.network, orderInfo.intent,
         orderInfo.delegateType, orderInfo.deadline, orderInfo.currency, orderInfo.dataMask,
         orderInfo.items.length, orderItemPattern, orderInfo.fees.length, feeItemPattern]
   ));
   

     const msg = orderHash;
     const params = [msg,account];
     const method = "personal_sign";
     console.log('--');
     const signature_ = await library.provider.request({method, params, } );
   
    const signature = ethers.utils.splitSignature(signature_);
     console.log('signature',signature);
     console.log('orderInfo',JSON.stringify(orderInfo));


    // 组合出线下签名的卖单数据
   const offChainOrder = {
       ...orderInfo,
       r: signature.r,
       s: signature.s,
       v: signature.v,
       signVersion: 1 // 写死为1
     };
     this.orderForServer=orderInfo;
     this.orderForServer.order_hash=orderHash;
     this.orderForServer.signature=signature_;
     this.orderForServer.items=this.getListforServer();
     this.orderForServer.create_at=getNow();

     this.orderForServer.delegate_type =orderInfo.delegateType;
     this.orderForServer.data_mask =orderInfo.dataMask;



     return offChainOrder;

   

};

