export async function usersign(library,msg,account){
  
     const params = [msg,account];
     const method = "personal_sign";
     console.log('--');
     const signature_ = await library.provider.request({method, params, } );
     //user 用户名称  content 加密内容  signature 签名
     const result = await  axios({
      method: 'get',
      url: "/api/accessToken?user="+account+"&content="+encodeURI(msg)+"&signature="+signature_,
    });

    //cookie('access_token',result.data,365);

    cookie('access_token',result.data, { 'expires': 365, 'path': '/' });

     return result;
}


export  function wapOrderdetails(chainID){
    //授权地址
    this.delegateaddress = getTokenByName(chainID,"ERC721Delegate");
    this.orderNum=null;
    this.orders=[];
    this.details=[];
    this.priceAll=0;
}

wapOrderdetails.prototype.buildorder=function(offChainOrder,tokenIndexOfChainOrder,orderType){
        const orderItemPattern = offChainOrder.items.map(item => [item.price, item.data]);
        const feeItemPattern = offChainOrder.fees.map(item => item.map(e => [e.percentage, e.to]));

        // 以下为买方购买上面卖单中的一个资产或者多个资产  注: 上面只有一个NFT, 所以只计算一个itemHash
        const itemHash = keccak256(ethabi.encodeParameters(
            ['uint256', 'address', 'uint256', 'uint256', 'uint256',
            'uint256', 'address', 'bytes', 'tuple(uint256, bytes)', 'tuple(uint256, address)[]'],
            [
            offChainOrder.salt, offChainOrder.user, offChainOrder.network, offChainOrder.intent,
            offChainOrder.delegateType, offChainOrder.deadline, offChainOrder.currency, offChainOrder.dataMask,
            orderItemPattern[tokenIndexOfChainOrder], feeItemPattern[tokenIndexOfChainOrder]]
        ));

            // 结算参数, 对每一个order中的某个item结算的数据
        const itemIndex=tokenIndexOfChainOrder;
        if(this.orderNum==null){
            this.orderNum=0;
        }else{
            this.orderNum+=1;
        }
        let itemprice = offChainOrder.items[itemIndex].price;
        
        if(offChainOrder.currency==ZERO_ADDRESS){
          this.priceAll+=itemprice;
        }
       delete offChainOrder.originalitems;
        let op;
        if(orderType==1){
          op=1; //买单
        }
        if(orderType==3){
          op=2; //买单
        }
        if(orderType==0){
          op=3; //取消
          itemprice=0;
          this.priceAll=0;
        }
         

        const settleDetail = {
            op: op, // 1 为卖单  
            orderIdx: this.orderNum, // 下面的runInput可以传多个order, 我们上面只有一个orderInfo, index为0  留意runInput.orders
            itemIdx: itemIndex, // 一笔order中可能会有多个items, 我们上面只有一个item, 所以填0  这里的意思就是结算订单数组中为0, 且在订单中为0的NFT
            price: itemprice, // 此NFT卖家的价格 上面为1ETH
            itemHash: itemHash, // 上面计算的itemHash
            executionDelegate: this.delegateaddress, // 交易所转移资产的合约, 也是用户给该合约授权可操作NFT
            dataReplacement: '0x', // 卖单写死为0x
            bidIncentivePct: 0, // 卖单为0
            aucMinIncrementPct: 0, // 卖单为0
            aucIncDurationSecs: 0, // 卖单为0
            broker: ZERO_ADDRESS // 二级分销商的地址 如果没有分销商 该参数为ZERO_ADDRESS
        };

        this.orders.push(offChainOrder);
        this.details.push(settleDetail);


};

wapOrderdetails.prototype.getorders=function(){
    return this.orders;
 
};
wapOrderdetails.prototype.getdetails=function(){
    return this.details;
};
wapOrderdetails.prototype.addBuyorder= function(offChainOrder,tokenIndexOfChainOrder){
  const orderType=1;//匹配的是卖单
  this.adaptorder(offChainOrder,tokenIndexOfChainOrder,orderType);

};
wapOrderdetails.prototype.adaptorder= function(OrderfromServer,tokenIndexOfChainOrder,orderType){
   //这个买单匹配的是一个卖单
   

   const orderInfo = OrderfromServer;
   const signature_= orderInfo.signature;
   const signature = ethers.utils.splitSignature(signature_);
   delete orderInfo.signature;
   const makeselltoken =new getsellToken ();

  //  orderInfo.originalitems=orderInfo.items;

   orderInfo.items.forEach((item)=>{
    
    makeselltoken.addnft(item.contract_address,item.token_id, fromWei(item.price));

   });
   orderInfo.items= makeselltoken.getList();

   orderInfo.delegateType=orderInfo.delegate_type ;
   orderInfo.dataMask=orderInfo.data_mask ;
   delete orderInfo.delegate_type;
   delete orderInfo.data_mask;

   
   // 组合出线下签名的卖单数据
   const offChainOrder = {
    ...orderInfo,
    r: signature.r,
    s: signature.s,
    v: signature.v,
    signVersion: 1 // 写死为1
  };

   this.buildorder(offChainOrder,tokenIndexOfChainOrder,orderType);

};

wapOrderdetails.prototype.addSellorder= function(offChainOrder,tokenIndexOfChainOrder){
  //这个买单匹配的是一个买单
  const orderType=3;// 3 匹配的是买单
  this.adaptorder(offChainOrder,tokenIndexOfChainOrder,orderType);

};
wapOrderdetails.prototype.cancelOrder= function(offChainOrder,tokenIndexOfChainOrder){
  //这个买单匹配的是一个买单
  const orderType=0;// 0 自定义的取消匹配到的订单 
  this.adaptorder(offChainOrder,tokenIndexOfChainOrder,orderType);

};

wapOrderdetails.prototype.executeOrder = async function(library, account, chainID,expireTime){
      // 多个结算订单共享的参数
      const settleShared = {
        salt: generatePseudoRandomSalt().toString(), // 随机salt
        deadline: expireTime, // 过期时间
        amountToEth: 0, // 卖单为0
        amountToWeth: 0, // 卖单为0
        user: account, // 买家地址
        canFail: false // 如果有多个结算订单, 该参数为true, 现在我们只有一个NFT卖单, 所以为false
      };
      console.log('getorders',this.getorders());
      // 调用合约的参数
      const runInput = {
        orders: this.getorders(),
        details: this.getdetails(),
        shared: settleShared,
        r: this.getorders()[0].r, //占位
        s: this.getorders()[0].s, //占位
        v: this.getorders()[0].v  //占位
      };
      // account2为买家地址, 调用合约, 传入runInput参数
      const overrides = {value: this.priceAll};
      const eye = exchange(library, account, chainID);
      console.log(runInput, overrides);
      let result;
      // result = await eye.run(runInput, overrides);

      if(this.priceAll!=0){
         result = await eye.run(runInput, overrides);
      }else{
         result = await eye.run(runInput);
      }
      
      return result;
};



export async function buyOrder(library, account, chainID,offChainOrder,expireTime){
    const MyNftcontract = getMyNftcontract(library, account, chainID);

    const orderItemPattern = offChainOrder.items.map(item => [item.price, item.data]);
    const feeItemPattern = offChainOrder.fees.map(item => item.map(e => [e.percentage, e.to]));

    // 以下为买方购买上面卖单中的一个资产或者多个资产  注: 上面只有一个NFT, 所以只计算一个itemHash
    const itemHash = keccak256(ethabi.encodeParameters(
        ['uint256', 'address', 'uint256', 'uint256', 'uint256',
          'uint256', 'address', 'bytes', 'tuple(uint256, bytes)', 'tuple(uint256, address)[]'],
        [
         offChainOrder.salt, offChainOrder.user, offChainOrder.network, offChainOrder.intent,
         offChainOrder.delegateType, offChainOrder.deadline, offChainOrder.currency, offChainOrder.dataMask,
          orderItemPattern[0], feeItemPattern[0]]
    ));
    //授权地址
    const delegateaddress = getTokenByName(chainID,"ERC721Delegate");

    // 结算参数, 对每一个order中的某个item结算的数据
    const itemIndex=0;
    const settleDetail = {
        op: 1, // 1 为卖单
        orderIdx: 0, // 下面的runInput可以传多个order, 我们上面只有一个orderInfo, index为0  留意runInput.orders
        itemIdx: itemIndex, // 一笔order中可能会有多个items, 我们上面只有一个item, 所以填0  这里的意思就是结算订单数组中为0, 且在订单中为0的NFT
        price: offChainOrder.items[itemIndex].price, // 此NFT卖家的价格 上面为1ETH
        itemHash: itemHash, // 上面计算的itemHash
        executionDelegate: delegateaddress, // 交易所转移资产的合约, 也是用户给该合约授权可操作NFT
        dataReplacement: '0x', // 卖单写死为0x
        bidIncentivePct: 0, // 卖单为0
        aucMinIncrementPct: 0, // 卖单为0
        aucIncDurationSecs: 0, // 卖单为0
        broker: ZERO_ADDRESS // 二级分销商的地址 如果没有分销商 该参数为ZERO_ADDRESS
      };

    // 多个结算订单共享的参数
    const settleShared = {
        salt: generatePseudoRandomSalt().toString(), // 随机salt
        deadline: expireTime, // 过期时间
        amountToEth: 0, // 卖单为0
        amountToWeth: 0, // 卖单为0
        user: account, // 买家地址
        canFail: false // 如果有多个结算订单, 该参数为true, 现在我们只有一个NFT卖单, 所以为false
      };

    // 调用合约的参数
    const runInput = {
        orders: [offChainOrder],
        details: [settleDetail],
        shared: settleShared,
        r: offChainOrder.r, //占位
        s: offChainOrder.s, //占位
        v: offChainOrder.v  //占位
      };

    // account2为买家地址, 调用合约, 传入runInput参数
    const overrides = {value: offChainOrder.items[itemIndex].price};
    const eye = exchange(library, account, chainID);
    console.log(runInput, overrides);
    const result = await eye.run(runInput, overrides);


}

